import { Component, OnInit, forwardRef, Input, Injector } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { AnyAppModelControl } from '../model-control';

@Component({
  selector: 'aa-comp-slider',
  template: `<aa-comp-label *ngIf="label">
    {{label}}
  </aa-comp-label>
  
<mat-slider [min]="min" [max]="max" [step]="step" #input [(ngModel)]="innerValue" 
  [disabled]="disabled"></mat-slider>

<mat-hint *ngIf="hint">{{hint}}</mat-hint>


<aa-comp-error [errorControl]="formControl"></aa-comp-error>`,
  styles: [``],
  providers: [
    { 
      provide: NG_VALUE_ACCESSOR, //Angular internaly injects all values that are registered on the NG_VALUE_ACCESSOR token
      useExisting: forwardRef(() => SliderComponent),
      multi: true
    }
  ]
})
export class SliderComponent extends AnyAppModelControl implements OnInit {
  @Input() label: string;
  @Input() hint: string;

  @Input() min: number;
  @Input() max: number;
  @Input() step: number;

  constructor(_injector: Injector) { 
    super(_injector);
  }

  ngOnInit() {
    this.initializeForm();
  }

}
