import { Component, OnInit, Input, forwardRef, Injector } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { AnyAppModelControl } from '../model-control';

@Component({
  selector: 'aa-comp-input',
  template: `<aa-comp-label *ngIf="label">
  {{label}}
</aa-comp-label>

<mat-form-field>
  <input matInput [placeholder]="placeholder" #input [type]="type" [(ngModel)]="innerValue" 
    [errorStateMatcher]="matcher" [disabled]="disabled">

  <mat-hint align="start" *ngIf="hint">{{hint}}</mat-hint>
  <mat-hint align="end" *ngIf="maxLength">{{input.value.length}} / {{maxLength}}</mat-hint>

  
  <aa-comp-error [errorControl]="formControl"></aa-comp-error>

</mat-form-field>
<!--[formControl]="form?.controls[formControlName]"-->`,
  styles: [``],
  providers: [
    { 
      provide: NG_VALUE_ACCESSOR, //Angular internaly injects all values that are registered on the NG_VALUE_ACCESSOR token
      useExisting: forwardRef(() => InputComponent),
      multi: true
    }
  ]
})
export class InputComponent extends AnyAppModelControl implements OnInit, ControlValueAccessor {  
  @Input() label: string;
  @Input() placeholder: string;
  @Input() type: string = "text";
  @Input() hint: string;

  @Input() maxLength: number = null;

  constructor(_injector: Injector) { 
    super(_injector);
  }

  ngOnInit() {
    this.initializeForm();
  }
}
