import { Component, OnInit, Input, Output, EventEmitter, Injector } from '@angular/core';
import { AnyAppTreeModel, AnyAppEventModel, AnyAppEventKeys } from '../../components.model';
import { AnyAppBaseControl } from '../../base-control';

@Component({
  selector: 'aa-comp-side-menu-item',
  template: `<div *ngIf="item" (click)="clickMenuItem()">
  <button mat-button [class.sub]="level > 0">
    
    <span>{{item.text}}</span>
    
    <mat-icon *ngIf="item.children?.length > 0">
        {{item.expanded ? 'keyboard_arrow_down' : 'keyboard_arrow_right'}}
    </mat-icon>
  </button>
</div>`,
  styles: [`button{width:100%;text-align:left}button:not(.sub) span{text-transform:uppercase}mat-icon{position:absolute;top:0;right:0;margin:4px}.sub{font-weight:400}`]
})
export class SideMenuItemComponent extends AnyAppBaseControl implements OnInit {
  @Input() item: AnyAppTreeModel;
  @Input() level: number;

  constructor(_inject: Injector) {
    super(_inject);
  }

  ngOnInit() {
  }

  clickMenuItem() {    
    if(this.item.children && this.item.children.length > 0) {
      this.item.expanded = !this.item.expanded;
    }

    if(this.item.navigateUrl != null) {
      this.onEvent.emit(new AnyAppEventModel<AnyAppTreeModel>(AnyAppEventKeys.MENU_ITEM_CLICK, this.item));
    }
  }

}
