export class AnyAppEventModel<T = {}> {
	key: AnyAppEventKeys;
	value?: T;

	constructor(key: AnyAppEventKeys, value?: T) {
		this.key = key;
		this.value = value;
	}
}

export class AnyAppMenuItem {
	text: string;
	key: string;
}

export class AnyAppTreeModel {
    public text: string;
    public children: AnyAppTreeModel[];
    public Id: number;
    public expanded?:boolean;
    public navigateUrl?: string;
    
    constructor(id: number, text: string, children?: AnyAppTreeModel[], expanded?: boolean, navigateUrl?: string) {
        this.text = text;
        this.children = children;
        this.Id = id;
        this.expanded = expanded;
        this.navigateUrl = navigateUrl;
    }
    
    toggle(){
        this.expanded = !this.expanded;
    }
}

export enum AnyAppEventKeys {
	MESSAGE_CLICK = 1,
	MENU_ITEM_CLICK = 2,
	MENU_FACE_CLICK = 3,
	MENU_NOTIFICATION_CLICK = 4,
}